@import url(https://fonts.googleapis.com/css?family=Montserrat);
html {
    box-sizing: border-box;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  
  * {
    margin: 0;
    padding: 0;
  }
  
  body {
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(rgba(196, 102, 0, 0.6), rgba(155, 89, 182, 0.6));
    min-height: 100vh;
    font-size: 16px;
  }
  
  ::-webkit-scrollbar {
    -webkit-appearance: none;
    width: 7px;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 4px;
    background-color: rgba(0, 0, 0, 0.3);
    box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
    -webkit-box-shadow: 0 0 1px rgba(255, 255, 255, 0.5);
  }
  
.App {
  font-family: montserrat, arial, verdana;
  width: 90vw;
  max-width: 1100px;
  margin: 0;
}

.gridContainer {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.05);
  min-height: 600px;
}

.gridPosts {
  flex: 2 1;
  min-height: inherit;
  margin: 1rem 3rem 0 2rem;
}

.gridForm {
  flex: 1 1;
  margin-right: 2rem;
}

@media (max-width: 1160px) {
  .gridContainer {
    display: grid;
    grid-template-columns: 1;
    grid-template-areas:
      'form'
      'posts';
    margin: 2rem 1rem;
  }

  .gridForm {
    grid-area: form;
    margin: 4rem auto;
  }

  .gridPosts {
    grid-area: posts;
    margin: 0;
    width: 85vw;
    margin-bottom: 4rem;
  }
}

@media (max-width: 830px) {
  .App {
    width: 95vw;
  }
}

@media (max-width: 800px) {
  .gridForm {
    margin: 4rem auto;
  }

  .gridPosts {
    margin: 0;
    width: 90vw;
    margin-bottom: 4rem;
  }
}

@media (max-width: 400px) {
  .gridContainer {
    margin: 0.5rem 0;
  }
}

.posts {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 0.5fr));
  grid-template-rows: auto;
  grid-gap: 25px;
  justify-content: flex-start;
}

.posts-item-wrapper {
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(50px);
          backdrop-filter: blur(50px);
  padding: 1rem;
  border-radius: 7px;
}

.posts-item {
  text-transform: uppercase;
  height: 120px;
  padding: 0.9rem 0.3rem 2rem 0.8rem;
  background-color: rgba(255, 255, 255, 0.8);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}

.posts,
.posts-item > ul {
  list-style-type: none;
}

.posts-item > ul > li {
  margin-right: 0.5rem;
  overflow-wrap: break-word;
}

.posts-item-heading {
  font-size: 1.25rem;
}

.posts-item-subheading {
  font-size: 1rem;
}

.posts-item-summary {
  text-transform: capitalize;
  font-size: 0.9rem;
  padding-top: 0.8rem;
}

.posts-item-summary > textarea {
  background-color: transparent;
  outline: none;
  border: none;
  resize: none;
  width: 100%;
  overflow-y: scroll;
  padding: 0 0.4rem 0.1rem 0;
}

@media (max-width: 1160px) {
  .posts {
    grid-template-columns: repeat(auto-fit, minmax(270px, 0.25fr));
    justify-content: center;
    grid-gap: 30px;
  }
}

.form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.2);
  -webkit-backdrop-filter: blur(80px);
          backdrop-filter: blur(80px);
  padding: 1.5rem;
  border-radius: 7px;
}

.msform {
  display: flex;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.8);
  width: 370px;
  height: auto;
  box-shadow: 0 10px 35px rgba(0, 0, 0, 0.5);
}

.msform-fieldset {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  border: none;
}

.msform-fieldset>h2 {
  text-transform: uppercase;
  margin-top: 2.2rem;
}

.msform-fieldset>h3 {
  font-size: 0.9rem;
  width: 90%;
  margin: 0.5rem 0 1.8rem;
  text-align: center;
}

.msform-fieldset>h4 {
  display: flex;
  align-self: flex-start;
  font-size: 0.76rem;
  color: #ff0000;
  margin: 0.4rem 0 1.5rem 1.5rem;
}

.msform-fieldset-div {
  position: relative;
  display: flex;
  flex-direction: column;
  width: 85%;
}

label {
  display: flex;
  align-self: flex-start;
  font-size: 0.85rem;
  font-weight: 600;
  margin: 0 0 0.4rem 0.1rem;
}

.msform-fieldset-div>input,
textarea {
  padding: 0.8rem 0 0.8rem 0.5rem;
  margin-bottom: 1.5rem;
}

.msform-fieldset-div>input::-webkit-input-placeholder,
textarea::-webkit-input-placeholder {
  font-size: 0.8rem;
  opacity: 0.8;
}

.msform-fieldset-div>textarea {
  font-family: arial;
  resize: vertical;
  min-height: 60px;
  max-height: 90px;
  padding: 0.9rem 0 0.8rem 0.5rem;
}

.msform-field {
  outline: 1px solid rgba(0, 0, 0, 0.4);
  border: none;
  width: 100%;
  border-radius: 3px;
}

.alert {
  display: flex;
  align-self: flex-start;
  font-size: 0.8rem;
  color: #ff0000;
  margin: -1.2rem 0 1.5rem 0rem;
}

.error-icon {
  color: #ff0000;
}

.success-icon {
  color: rgba(39, 174, 96, 1);
}

.input-error-icon,
.input-success-icon {
  position: absolute;
  top: 31px;
  right: 10px;
}

.textarea-error-icon,
.textarea-success-icon {
  position: absolute;
  top: 38px;
  right: 10px;
}

.msform-fieldset>button {
  width: 85%;
  padding: 0.8rem 0;
  font-family: inherit;
  font-weight: 600;
  cursor: pointer;
  margin: 0.7rem 0 3rem;
  background-color: rgba(39, 174, 96, 1);
  color: #fff;
  border: none;
  border-radius: 3px;
}

.msform-fieldset>button:hover {
  background-color: rgba(39, 174, 96, 0.9);
}

@media (max-width: 500px) {
  .msform {
    width: 70vw;
  }

  .msform-fieldset {
    width: 60vw;
    text-align: center;
  }

  .msform-fieldset-div {
    width: 100%;
  }

  .msform-fieldset>button {
    width: 100%;
  }

  .msform-field {
    width: 60vw;
  }
}

@media (max-width: 400px) {
  .msform {
    width: 75vw;
  }

  .msform-fieldset {
    width: 65vw;
  }

  .msform-field {
    width: 65vw;
  }
}
